import React from 'react'
import { useRef, useState, useEffect } from 'react'
import StatusIcon from './Feedback/StatusIcon'
import Toast from './Feedback/Toast'
import Tooltip from './Feedback/Tooltip'
import FigmaLink from './FigmaLink'
import faker from 'faker'
import Image from './Feedback/Image'
import { useSpring, animated } from 'react-spring'
import ToggleBtn from './ToggleBtn'
import UXNotes from './Feedback/Notes'


const ToastShowcase = () => {

  //States
  const [toast, setToast] = useState([])
  const [status, setStatus] = useState("success")
  const [message, setMessage] = useState(faker.hacker.phrase())
  const [button, setButton] = useState('')
  const [close, setClose] = useState(false)
  const [big, setBig] = useState(false)
  const [wordCount, setWordCount] = useState()
  const [press, setPress] = useState(true)

  //Reference
  const container = useRef()
  const count = useRef(1)

  //Gap between toast
  const gap = 8

  //Move api
  const [move, api] = useSpring(() => ({ y: 0 }))
  const configuration = { tension: 170, friction: 26 }

  //Snap toast container to offset removed toast's height
  const updateToast = (id, height, snap) => {
    let go = document.getElementById(id)
    if (go !== null) {
      api.start({ y: container.current.offsetHeight - height - gap, config: snap ? { duration: 0 } : configuration })
      go.remove()
    }
  }

  const notes = [
    {
      title: "Auto-dismiss",
      description: "Duration of the toast varies with the word length of the text content. Mininum duration of 3500ms and maximum of 10000ms. Alternatively toast could be dismissed by clicking the close button"
    },
    {
      title: "Persist",
      description: "Countdown timer stops when the toast is hovered by mouse or receiving keyboard focus. The countdown will resume once the toast loses focuses."
    },
    {
      title: "Multi Toasts",
      description: "When there are multiple toast notifications appears, stack them vertically, with the newest on the top. Ideally, only max. 2 toasts display at a time. That says, when the 3rd toast move in from the top, the 1st toast will get slightly pushed down and disappear immediately regardless of the countdown timer."
    },
    {
      title: "Cursor",
      description: "Cursor change according to the type of content in the tooltip. Default arrow, text for text content and pointer for link/button"
    },
  ]

  //Manual toast remove function
  const removeToast = (toast) => {
    if (toast.previousSibling) api.start({ y: container.current.offsetHeight - toast.offsetHeight - gap, config: configuration })
    //Unmount toast from DOM
    setTimeout(() => {
      updateToast(toast.id, toast.offsetHeight, true)
    }, 500)
    toast.classList.add('fade')
  }

  const duration = (message) => {
    const min = 3500
    const max = 10000
    const words = message.split(" ").length
    const cal = words * 350
    if (cal < min) return min
    else if (cal > max) return max
    else return Math.round(cal / 100) * 100
  }

  const checkLimit = (num) => {
    let total = container.current.childNodes
    let first = container.current.firstChild

    if (first && total.length > num) {
      let last = document.getElementById(first.id)
      removeToast(last)
    }
  }

  const pushToast = () => {
    if (press) {
      let id = count.current
      count.current++

      setPress(false)

      checkLimit(1)

      setToast([...toast, <Toast destroy={removeToast} key={id} id={`t${id}`} status={status} message={message} button={button} close={close} duration={duration(message)} update={updateToast} content={big} />])

      setTimeout(() => {
        setPress(true)
      }, 500)

      setMessage(faker.hacker.phrase())
    }
  }

  //Status onClick / onChange event
  const changeStatus = (e) => {
    e.target.id === status ? setStatus(false) : setStatus(e.target.id)
  }

  //Push new toast down to be visible within viewport
  useEffect(() => {
    api.start({ y: container.current.offsetHeight, config: { tension: 170, friction: 26 } })
  }, [toast, api])

  useEffect(() => {
    const preview = document.getElementById('preview-toast')
    preview.offsetHeight > 36 && message.length > 30 ? setBig(true) : setBig(false)
  }, [message, setMessage, button, setButton, status, setStatus, close, setClose, big, setBig])

  useEffect(() => {
    message.length < 1 ? setWordCount(0) : setWordCount(message.split(" ").length)
  }, [message])

  return (
    <div className="category-container stack" style={{ gap: "2rem" }}>
      <div className="toastlist-container">
        <animated.div style={move} ref={container} className="toastlist">
          {toast}
        </animated.div>
      </div>
      <div className="description-container">
        <p>A toast displays short and time-based feedback, which overlays any contents in the page. It is meant to be noticed without disrupting a user's experience or requiring an action to be taken.</p>
      </div>
      <div className="types-container">
        <div className="default-container stack" style={{ gap: "1rem" }}>
          <p className="title">Default</p>
          <div className="type container">
            <Toast status="success" message="Here's a toast message" button="Button" close={true} still={true} />
          </div>
        </div>
        <div className="content-container stack" style={{ gap: "1rem" }}>
          <p className="title">Small with Content</p>
          <div className="type container">
            <Toast status="warning" message="The default text maximum width is 320px, extreme case will fold display." content={true} button="Button" close={true} still={true} />
          </div>
        </div>
      </div>
      <div className="preview toast stack" style={{ gap: "1rem" }}>
        <p className="title">Demo Preview</p>
        <div className="preview-container toast">
          <Toast id="preview-toast" content={big} status={status} message={message} button={button} close={close} still={true} />
        </div>
      </div>
      <div className="control-container stack" style={{ gap: "1rem" }}>
        <div className="input toast">
          <div className="radio-container toast">
            <div className="container">
              <div className="top-container"><p>Status</p></div>
              <div className="bottom-container">
                <input onChange={changeStatus} onClick={changeStatus} type="radio" name="status" id="success" checked={status === 'success' ? true : false} />
                <label htmlFor="success"><StatusIcon status="success" /></label>
                <input onChange={changeStatus} onClick={changeStatus} type="radio" name="status" id="info" checked={status === 'info' ? true : false} />
                <label htmlFor="info"><StatusIcon status="info" /></label>
                <input onChange={changeStatus} onClick={changeStatus} type="radio" name="status" id="warning" checked={status === 'warning' ? true : false} />
                <label htmlFor="warning"><StatusIcon status="warning" /></label>
                <input onChange={changeStatus} onClick={changeStatus} type="radio" name="status" id="error" checked={status === 'error' ? true : false} />
                <label htmlFor="error"><StatusIcon status="error" /></label>
                <input onChange={changeStatus} onClick={changeStatus} type="radio" name="status" id="spinner" checked={status === 'spinner' ? true : false} />
                <label htmlFor="spinner"><StatusIcon status="spinner" spinning={false} /></label>
              </div>
            </div>
          </div>
          <div className="textfield-container toast">
            <div className="container">
              <div className="top-container"><p>Message</p></div>
              <div className="bottom-container coral">
                <input
                  onChange={e => setMessage(e.target.value) && setWordCount(message.split(" ").length)}
                  value={message}
                  type="text"
                  placeholder="Type your toast message here!"
                  name="message"
                  id="message" />
              </div>
            </div>
            <div className="close-options">
              <div className="container">
                <div className="top-container"><p>Button</p></div>
                <div className="bottom-container coral">
                  <input
                    onChange={e => setButton(e.target.value)}
                    value={button}
                    type="text"
                    placeholder="Button"
                    name="button"
                    id="button" /></div>
              </div>
              <div className="checkbox-container toast " style={{ flex: 1 }}>
                <div className="container">
                  <div className="top-container"><p>Icon</p></div>
                  <div className="bottom-container" style={{ position: "relative" }}>
                    <input
                      type="checkbox"
                      name="closebutton"
                      id="close"
                      checked={close}
                      onChange={() => setClose(!close)} />
                    <label htmlFor="close"><StatusIcon status="close" /></label>
                  </div>
                </div>
              </div>
            </div>
            <button onClick={pushToast} >Push</button>
          </div>
        </div>
        <div className="property-container stack" style={{ gap: ".25rem" }}>
          <p>Word Count: {wordCount}</p>
          <p>Duration: {duration(message) / 1000}s</p>
        </div>
      </div>
      <div className="notes stack" style={{ gap: "1rem" }}>
        <p className="title">UX Notes</p>
        <ul className="notes-container stack" style={{ gap: "1rem" }}>
          {notes.map((note, i) => (
            <UXNotes key={i} id={i} title={note.title} description={note.description}></UXNotes>
          ))}
        </ul>
      </div>
      <FigmaLink url="https://www.figma.com/file/CHJcwbBlyx7pIYmiPkno3R/?node-id=432%3A28076" />
      <hr />
    </div>
  )
}

const TooltipShowcase = () => {

  const [offsetSize, setOffsetSize] = useSpring(() => ({ opacity: 0 }))
  const [originName, setOriginName] = useState('bottom')
  const [dist, setDist] = useState(8)
  const [off, setOff] = useState(8)
  const [disableOff, setDisableOff] = useState(false)

  //Image Variant
  const [imageStatic, setImageStatic] = useState(true)

  //Multiple Lines Variant
  const [linesStatic, setLinesStatic] = useState(true)
  const multiplelines = `I inadvertently went to See's Candy last week (I was in the mall looking for phone repair), and as it turns out, See's Candy now charges a dollar -- a full dollar -- for even the simplest of their wee confection offerings. I bought two chocolate lollipops and two chocolate-caramel-almond things. The total cost was four-something. I mean, the candies were tasty and all, but let's be real: A Snickers bar is fifty cents. After this dollar-per-candy revelation, I may not find myself wandering dreamily back into a See's Candy any time soon.`

  const previews = [
    {
      origin: "top-left",
      name: "Top Start",
      component: `Tooltip is positioned at the top and align left to the tip`,
    },
    {
      origin: "top",
      name: "Top",
      component: `Tooltip is positioned at the top and align center to the tip`,
    },
    {
      origin: "top-right",
      name: "Top End",
      component: `Tooltip is positioned at the top and align right to the tip`,
    },
    {
      origin: "left-top",
      name: "Left Start",
      component: `Tooltip is positioned on the left and align to the top of the tip`,
    },
    {
      origin: "left",
      name: "Left",
      component: `Tooltip is positioned on the left and align to the center of the tip`,
    },
    {
      origin: "left-bottom",
      name: "Left End",
      component: `Tooltip is positioned on the left and align to the bottom of the tip`,
    },
    {
      origin: "bottom-left",
      name: "Bottom Start",
      component: `Tooltip is positioned at the bottom and align left to the tip`,
    },
    {
      origin: "bottom",
      name: "Bottom",
      component: `Tooltip is positioned at the bottom and align to the center of the tip`,
    },
    {
      origin: "bottom-right",
      name: "Bottom End",
      component: `Tooltip is positioned at the bottom and align right to the tip`,
    },
    {
      origin: "right-top",
      name: "Right Start",
      component: `Tooltip is positioned on the right and align to the top of the tip`,
    },
    {
      origin: "right",
      name: "Right",
      component: `Tooltip is positioned on the right and align to the center of the tip`,
    },
    {
      origin: "right-bottom",
      name: "Right End",
      component: `Tooltip is positioned on the right and align to the bottom of the tip`,
    },
  ]

  const notes = [
    {
      title: "Placement",
      description: "By default, the tooltip is placed at the top of the paired element. Its tip points to the center of the paired element. There should be a margin of at least 8px."
    },
    {
      title: "Persist",
      description: "Moving the pointer within the paired element and moving the pointer over to the tooltip content."
    },
    {
      title: "Motion",
      description: "Animate originates from position of the tip with opacity and scaling. High tension and friction spring animation mechanism."
    },
    {
      title: "Dismiss",
      description: "Tooltip transit out in reverse to entry animation when the pointer moves out from its paried element or from the tooltip itself."
    },
  ]

  useEffect(() => {
    const previewer = document.querySelector('.origin-btn.preview')
    const w = previewer.offsetWidth
    const h = previewer.offsetHeight
    setOffsetSize.start({ height: h + Number(dist) * 2, width: w + Number(dist) * 2, opacity: 1, config: { tension: 1000 }, onRest: () => setOffsetSize.start({ opacity: 0, delay: 300, config: { tension: 80 } }) })
  }, [dist, setOffsetSize])

  useEffect(() => {
    switch (originName) {
      default:
      case "bottom-left":
      case "bottom-right":
      case "top-left":
      case "top-right":
        setOff(8)
        setDisableOff(false)
        break;
      case "bottom":
      case "top":
      case "right":
      case "left":
        setDisableOff(true)
        break
      case "left-top":
      case "left-bottom":
      case "right-top":
      case "right-bottom":
        setOff(24)
        setDisableOff(false)
        break;
    }
    setDist(8)
  }, [originName, setOriginName])

  return (
    <div className="category-container stack" style={{ gap: "2rem" }}>
      <div className="description-container">
        <p>A tooltip displays short messages that clarifies “What it is” or  “How to use”. It is typically only visibile on hover and is to be easily dismissed.</p>
      </div>
      <div className="preview-container stack" style={{ gap: "1rem" }}>
        <p className="title">Positioning</p>

        <div className="coral sample-container" >
          <div className="container stack" style={{ maxWidth: "40rem", gap: "2rem", margin: "0 auto" }}>
            <div className="row-container flex-container around" >
              {previews.filter((preview, index) => index <= 2).map((top, index) => (
                <Tooltip component={top.component} key={index} off={8} id={index} margin={dist} origin={top.origin}><div onClick={() => setOriginName(top.origin)} className={originName === top.origin ? 'origin-btn selected' : 'origin-btn'} style={top.origin.includes("left") ? { textAlign: "right" } : top.origin === "top" ? { textAlign: "center" } : null}>{top.name}</div></Tooltip>
              ))}
            </div>
            <div className="row-container flex-container between">
              <div className="col stack" style={{ gap: "2rem", alignItems: "flex-end" }}>
                {previews.filter((preview, index) => index >= 3 && index <= 5).map((left, index) => (
                  <Tooltip component={left.component} key={index} off={24} id={index} margin={dist} origin={left.origin}><div onClick={() => setOriginName(left.origin)} className={originName === left.origin ? 'origin-btn selected' : 'origin-btn'} style={{ textAlign: "right" }}>{left.name}</div></Tooltip>
                ))}
              </div>
              <div className="col stack center middle" style={{ flex: 1 }}>
                <div className="container">
                  {previews.map((preview, i) => (
                    originName === preview.origin
                    && <Tooltip key={i + 12} margin={dist} off={off} id={i + 7} origin={preview.origin} always={true} component={preview.name}>
                      <div className="origin-btn preview">Static Tooltip
                        <animated.div className="offsetter" style={offsetSize} />
                      </div>
                    </Tooltip>
                  ))}
                </div>
              </div>
              <div className="col stack" style={{ gap: "2rem" }}>
                {previews.filter((preview, index) => index >= 9).map((right, index) => (
                  <Tooltip component={right.component} key={index} off={24} id={index} margin={dist} origin={right.origin}><div onClick={() => setOriginName(right.origin)} className={originName === right.origin ? 'origin-btn selected' : 'origin-btn'}>{right.name}</div></Tooltip>
                ))}
              </div>
            </div>
            <div className="row-container flex-container around">
              {previews.filter((preview, index) => index >= 6 && index <= 8).map((bottom, index) => (
                <Tooltip component={bottom.component} key={index} id={index} off={8} margin={dist} origin={bottom.origin}><div onClick={() => setOriginName(bottom.origin)} className={originName === bottom.origin ? 'origin-btn selected' : 'origin-btn'} style={bottom.origin.includes("left") ? { textAlign: "right" } : bottom.origin === "bottom" ? { textAlign: "center" } : null}>{bottom.name}</div></Tooltip>
              ))}
            </div>
          </div>
        </div>
        <div className="control-container flex-container" style={{ gap: "2rem" }}>
          <div className="slider-container">
            <p>Tooltip Offset</p>
            <input type="range" onChange={(e) => setDist(e.target.value)} value={dist} name="gap" min="0" max="64" id="gap" />
            <label htmlFor="gap">{dist}px</label>
          </div>
          <div className="slider-container">
            <p style={disableOff ? { opacity: .35 } : {}}>Tip Offset (Only for non-centered tip variants)</p>
            <input disabled={disableOff} type="range" onChange={(e) => setOff(e.target.value)} value={off} name="off" min="0" max="100" id="off" />
            <label style={disableOff ? { opacity: .35 } : {}} htmlFor="off">{off}%</label>
          </div>
        </div>
        <div className="notes stack" style={{ gap: "1rem" }}>
        <p className="title">UX Notes</p>
        <ul className="notes-container stack" style={{ gap: "1rem" }}>
          {notes.map((note, i) => (
            <UXNotes key={i} id={i} title={note.title} description={note.description}></UXNotes>
          ))}
        </ul>
      </div>
      </div>
      <div className="variants-container stack" style={{ gap: "2rem" }}>
        <p className="title">Variants</p>
        <div className="image-tooltip-container stack" style={{ gap: "1rem" }}>
          <div className="container flex-container between">
            <p>Image</p>
            <ToggleBtn id={"staticimagechecker"} checked={imageStatic} click={() => setImageStatic(!imageStatic)}><p style={{ fontSize: "1rem" }}>{imageStatic ? "Static" : "Hover"}</p></ToggleBtn>
          </div>
          <div className="coral sample-container stack middle" style={{ zIndex: 200} }>
            <Tooltip key={imageStatic ? "staticImg" : "hoverImg"} image={true} relative={imageStatic ? true : false} always={imageStatic ? true : false} component={
              <div className="container stack" style={{ gap: ".5rem" }}>
                <Image />
                <div className="text-container">
                  I'll input the haptic PNG protocol, that should panel the XML application!
                </div>
              </div>
            }>
              <div className="container gridcenter" style={{ width: "100%" }}>
                <div className="img-container" style={{ cursor: "pointer", maxWidth: 96 }}>
                  <Image />
                </div>
              </div>
            </Tooltip>
          </div>
        </div>
        <div className="multiple-lines stack" style={{ gap: "1rem" }}>
          <div className="container flex-container between">
            <p>Multiple Lines</p>
            <ToggleBtn id={"staticlinechecker"} checked={linesStatic} click={() => setLinesStatic(!linesStatic)}><p style={{ fontSize: "1rem" }}>{linesStatic ? "Static" : "Hover"}</p></ToggleBtn>
          </div>
          <div className="multiple-container stack" style={{ gap: "1rem" }}>
            <div className="coral sample-container stack middle" style={{ width: "100%" }}>
              <Tooltip key={linesStatic ? "staticLines" : "hoverLines"} relative={linesStatic ? true : false} always={linesStatic ? true : false} component={multiplelines}>
                <div className="container stack middle" style={{ width: "100%" }} >
                  <div className="container" style={{ maxWidth: 240, padding: "1rem", backgroundColor: "white" }}>
                    <p className="line-clamp three">
                      {multiplelines}
                    </p>
                  </div>
                </div>
              </Tooltip>
            </div>
          </div>
        </div>
      </div>

    </div>
  )
}

const AlertShowcase = () => {

  return (
    <>
      <h1>Alert</h1>
    </>
  )
}

export { ToastShowcase, TooltipShowcase, AlertShowcase }
